import * as React from "react";
import { Button, Col, Form, Row, Stack, Table, Alert } from "react-bootstrap";
import { Notes } from "./Notes";
import { NotesAligner } from "./NotesAligner";
import GroupBox from "../Common/GroupBox"

interface IAnswerFormProps {
  notes: string[];
}

const AnswerForm: React.FC<IAnswerFormProps> = ({ notes }) => {
  const [playedNotes, setPlayedNotes] = React.useState<string[]>([]);
  const [answerText, setAnswerText] = React.useState("");
  const [answerNotes, setAnswerNotes] = React.useState<string[]>([]);
  const [score, setScore] = React.useState("");
  const [useAlign, setUseAlign] = React.useState(true);

  const checkAnswer = () => {
    setPlayedNotes(notes);
    updateScore();
  };
  React.useEffect(() => {
    // Runs if useAlign changes
    if (playedNotes.length === 0) {
      return;
    }
    updateScore();
    // eslint-disable-next-line
  }, [useAlign]);

  //This should really be moved to a utility class and unit tested there, but instead
  //is currently tested in AnswerForm.test.tsx as an example on how to test React components
  function calculateScore(acutalNotes: string[], userAnsText: string, align: boolean): [string[], string] {
    let userNotes = userAnsText
      .split(/\s+/)
      .map((x) => x.replace(/\d/, "").toUpperCase())
      .slice(0, notes.length);

    if (align) {
      userNotes = NotesAligner.alignNotes(acutalNotes, userNotes);
    }
    const correct = userNotes.filter((val, index) => Notes.compareUserEntry(notes[index], val)).length;
    const percent = ((correct * 100.0) / notes.length).toFixed(2);
    return [userNotes, `Score: ${correct}/${notes.length} (${percent}%)`];
  }

  function updateScore() {
    const [userNotes, scoreText] = calculateScore(notes, answerText, useAlign);
    setAnswerNotes(userNotes);
    setScore(scoreText);
  }

  return (
    <Form className="mt-3">
      <GroupBox>
        <Row className="mb-3">
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Enter the notes you heard separated by spaces</Form.Label>
            <Form.Control data-testid="userAnswerText" placeholder="Enter notes" onChange={(event) => setAnswerText(event.target.value)} />
            <Form.Text className="text-muted">For example: C# D E F#</Form.Text>
          </Form.Group>
        </Row>

        <Row className="mb-3">
          <Col>
            <Stack direction="horizontal" gap={3}>
             
              <Button className="mt-3" data-testid="answer" onClick={checkAnswer} disabled={notes.length === 0}>
                Check Answer
              </Button>
            </Stack>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Form.Check
              type="checkbox"
              label="Align notes when scoring"
              checked={useAlign}
              onChange={(e) => setUseAlign(e.target.checked)}
            />
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Table responsive>
              <thead>
                <tr>
                  <th>Actual</th>
                  {Array.from({ length: playedNotes.length }).map((_, index) => (
                    <th key={index}>{playedNotes[index]}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Your Answer</td>
                  {Array.from({ length: answerNotes.length }).map((_, index) => (
                    <td
                      key={index}
                      style={{
                        backgroundColor: Notes.compareUserEntry(playedNotes[index], answerNotes[index]) ? "inherit" : "pink",
                      }}
                    >
                      {answerNotes[index]}
                    </td>
                  ))}
                </tr>
              </tbody>
            </Table>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Alert variant="success">
              <Alert.Heading data-testid="score">{score}</Alert.Heading>
            </Alert>
          </Col>
        </Row>
      </GroupBox>
    </Form>
  );
};

export default AnswerForm;
