import * as React from "react";
import { Col, Row, Container } from "react-bootstrap";
import InputForm from "./InputForm";
import AnswerForm from "./AnswerForm";
import { GeneratedNotes } from "./Notes";

interface IIntervalTrainer {}

const IntervalTrainer: React.FC<IIntervalTrainer> = () => {
  const [generatedNotes, setGeneratedNotes] = React.useState<GeneratedNotes>({ notes: [], durations: [] });

  return (
    <Container>
      <Row>
        <Col>
          <InputForm setGeneratedNotes={setGeneratedNotes} notes={generatedNotes.notes} noteDurations={generatedNotes.durations} />
        </Col>
      </Row>

      <Row>
        <Col>
          <AnswerForm notes={generatedNotes.notes} />
        </Col>
      </Row>
    </Container>
  );
};

export default IntervalTrainer;
