import { Notes } from "./Notes";

export class NotesAligner {
  private static readonly largeCost = 100000;

  //Generates the best alignment of userNotes to actual.
  public static alignNotes(actualNotes: string[], userNotes: string[]) {
    if (userNotes.length >= actualNotes.length) {
      return userNotes;
    }
    /* 
            We start from the back of the string. Assume we are at i on the actualNotes and j on the userNotes and C(x,y) gives us the cost of aligning
            actualNotes[0..x] and userNote[0..y]

            Then the cost of aligning  C(i,j) is the minimum of

                1. C(i-1,j-1) + actualNotes[i]===userNotes[j] ? 0:1 

                2. C(i-1,j) + 1 (insert a gap into userNote)

    */

    const cost = NotesAligner.getCostMatrix(actualNotes, userNotes);
    return NotesAligner.alignedNotesFromCost(actualNotes, userNotes, cost);
  }

  private static alignedNotesFromCost(actualNotes: string[], userNotes: string[], cost: number[][]) {
    let i = actualNotes.length - 1;
    let j = userNotes.length - 1;
    const alignedNotes: string[] = [];

    while (i >= 0) {
      if (j < 0) {
        alignedNotes.push("*");
        i--;
        continue;
      }

      const c1 = NotesAligner.getCost(cost, i - 1, j - 1) + NotesAligner.getNoteCost(actualNotes[i],userNotes[j]);
      const c2 = NotesAligner.getCost(cost, i - 1, j) + 1; //gap

      if (c2 < c1) {
        alignedNotes.push("*");
        i--;
      } else {
        alignedNotes.push(userNotes[j]);
        j--;
        i--;
      }
    }
    return alignedNotes.reverse();
  }

  private static getCostMatrix(actualNotes: string[], userNotes: string[]) {
    let cost: number[][] = [];

    for (let i = 0; i < actualNotes.length; i++) {
      let c = [];
      for (let j = 0; j < userNotes.length; j++) {
        if (j > i) {
          //If the user string is bigger than the actual string then
          //there is no way to align it (since we cannot delete from actual)
          c.push(NotesAligner.largeCost);
          continue;
        }

        if (i === 0 && j === 0) {
          c.push(NotesAligner.getNoteCost(actualNotes[i],userNotes[j]));
          continue;
        }

        const c1 = NotesAligner.getCost(cost, i - 1, j - 1) + NotesAligner.getNoteCost(actualNotes[i],userNotes[j]);
        const c2 = NotesAligner.getCost(cost, i - 1, j) + 1;
        c.push(Math.min(c1, c2));
      }
      cost.push(c);
    }
    return cost;
  }

  private static getNoteCost(actual:string, user:string){
    return Notes.compareUserEntry(actual,user)? 0: 1
  }

  private static getCost(cost: number[][], i: number, j: number) {
    if (j < 0 && i >= 0) {
      //The cost of matching an empty user string wtih a non empty actual string is the length of the actual string.
      return i + 1;
    }

    if (i < 0 || j < 0) {
      return NotesAligner.largeCost;
    }
    return cost[i][j];
  }
}
