import * as React from "react";

export class Utils {
  public static debugLog(msg: any) {
    if (typeof console !== "undefined" && console.log) console.log(msg);
  }

  // Returns a random number between min(included) and max (exluding)
  public static randomInteger(min: number, max: number) {
    return Math.floor(Math.random() * (max - min)) + min;
  }
}

export function useLocalState<S>(key: string, initialState: S | (() => S)): [S, React.Dispatch<React.SetStateAction<S>>] {
  const [value, setValue] = React.useState(() => {
    const storedValue = localStorage.getItem(key);
    if (storedValue) {
      try {
        const parsed = JSON.parse(storedValue) as S;
        if (parsed) {
          return parsed;
        }
      } catch {}
    }
    return initialState instanceof Function ? initialState() : initialState;
  });

  React.useEffect(() => {
    //called when value changes.
    localStorage.setItem(key, JSON.stringify(value));
  }, [key,value]);

  return [value, setValue];
}
