import {Config} from '../Config'
import {Utils} from '../Utils'


export type GeneratedNotes = {
    notes: string[];
    durations: string[];
  };

export class Notes {
    static readonly rawNotes = ["C","C#","D","D#","E","F","F#","G","G#","A","A#","B"];
    static notes:string[];
    static readonly intervals:[string,number] [] = [
        ["M2",2],
        ["M3",4],
        ["P4",5],
        ["P5",7],
        ["M6",9],
        ["M7",11],
        ["Octave",12],
    ];

    public static getNotes(){
        Notes.ensureNotes();
        return Notes.notes;
    }

    private static ensureNotes(){
        if(Notes.notes){
            return Notes.notes;
        }
        Notes.notes = Notes.rawNotes.map(x=> Notes.addOctaveToRawNote(x,Config.startingOctave) );
    }

    private static addOctaveToRawNote(rawNote:string, octave:number){
        return rawNote+octave;
    }

    public static generateNotes(rootNote:string, interval:string, numberOfNotes: number, useChromaticScale: boolean=false){
        const notes = Array.from(Notes.getAllPossibleNotes(rootNote,interval,useChromaticScale));
        if(notes.length===0){
            return [];
        }

        return Array.from({length:numberOfNotes}).map(i=>{
            const rndIdx = Math.floor(Math.random() * notes.length);
            return notes[rndIdx]
        });
    }

   

    public static generateRandomDurations(numberOfNotes: number){
        const durations = ["2n", "4n", "8n"];
        return Array.from({length:numberOfNotes}).map(i=>{
            const rndIdx = Utils.randomInteger(0,durations.length)
            return durations[rndIdx]
        });
    }

    public static getAllPossibleNotes(rootNote:string, interval:string, useChromaticScale: boolean=false){
        Notes.ensureNotes();
        const notes = new Set<string>();
        const rootNoteIdx = Notes.notes.findIndex((e)=>e===rootNote);
        if(rootNoteIdx <0){
            Utils.debugLog("Unexpected: Could not find note: " + rootNote);
            return notes;
        }
        const distanceIdx = Notes.intervals.findIndex((e)=>e[0]===interval);
        if(distanceIdx <0){
            Utils.debugLog("Unexpected: Could not find interval: " + interval);
            return notes;
        }
        
        notes.add(rootNote);
        if(useChromaticScale){
            const maxDistance = Notes.intervals[distanceIdx][1];
            for (let i = 0; i <= maxDistance; i++) {
                notes.add(Notes.getNoteAtDistance(rootNoteIdx,i));
            }
            return notes;
        }



        for (let i = 0; i <= distanceIdx; i++) {
            notes.add(Notes.getNoteAtDistance(rootNoteIdx, Notes.intervals[i][1]));
        }
        return notes;
    }

    private static getNoteAtDistance(rootNoteIdx: number, distance: number){
        const idx =  Math.trunc((rootNoteIdx+distance)%Notes.rawNotes.length);
        const octaveIdx = Math.trunc((rootNoteIdx+distance)/Notes.rawNotes.length);
        return Notes.addOctaveToRawNote(Notes.rawNotes[idx],Config.startingOctave+octaveIdx) ;
    }

    public static compareUserEntry(actualNote:string, userNote:string):boolean{
        return actualNote.replace(/\d/,'') === userNote
    }


}