import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import Header from "./IntervalTrainer/Header";
import { Tabs, Tab, Alert } from "react-bootstrap";
import { Player } from "./Player";
import IntervalTrainer from "./IntervalTrainer/IntervalTrainer";
import FretboardMaster from "./FretboardMaster/FretboardMaster";
import * as React from "react";
import { Utils } from "./Utils";

function App() {
  const [loaded, setLoaded] = React.useState(false);
  (async () => {
    try{
      await Player.initInstruments();
    }catch(error) {
      Utils.debugLog(`Exception calling initInstruments(). ${error} `)
    }
    setLoaded(true)
  })();

  return (
    <>
      <Header />
      {!loaded && <Alert variant="success">Loading Instruments...</Alert>}
      {loaded && (
        <Tabs defaultActiveKey="home" id="uncontrolled-tab-example" className="mb-3">
          <Tab eventKey="home" title="Interval Trainer">
            <IntervalTrainer />
          </Tab>

          <Tab eventKey="fretBoardMaster" title="Fretboard Master">
            <FretboardMaster />
          </Tab>
        </Tabs>
      )}
    </>
  );
}

export default App;
