import * as React from "react";
import { Button, Col, Form, Row, Stack } from "react-bootstrap";
import { Notes, GeneratedNotes } from "./Notes";
import { Config } from "../Config";
import { Player, MusicalInstrument } from "../Player";
import GroupBox from "../Common/GroupBox";

interface IInputFormProps {
  setGeneratedNotes: React.Dispatch<React.SetStateAction<GeneratedNotes>>;
  notes: string[];
  noteDurations: string[];
}

const InputForm: React.FC<IInputFormProps> = ({ setGeneratedNotes, notes, noteDurations }) => {
  const [rootNote, setRootNote] = React.useState(Notes.getNotes()[0]);
  const [interval, setInterval] = React.useState(Notes.intervals[0][0]);
  const [useRandomNoteDuration, setUseRandomNoteDuration] = React.useState(false);
  const [useChromaticScale, setUseChromaticScale] = React.useState(false);
  const [instrument, setInstrument] = React.useState<MusicalInstrument>("piano");

  const [numberOfNotes, setNumberOfNotes] = React.useState("" + Config.initialNumberOfNotes);

  function generateNotes() {
    const numNotes = parseInt(numberOfNotes);
    const notes = Notes.generateNotes(rootNote, interval, numNotes, useChromaticScale);
    const durations = useRandomNoteDuration ? Notes.generateRandomDurations(numNotes) : Array.from({ length: numNotes }).map((x) => "2n");

    setGeneratedNotes({ notes: notes, durations: durations });
    Player.playNotes(instrument, notes, durations);
  }

  function playRootNote() {
    Player.playNotes(instrument, [rootNote], ["2n"]);
  }

  return (
    <Row>
      <Col>
        <Form className="mt-3">
          <GroupBox>
            <Row className="mb-3">
              <Col xs={6} md={4}>
                <Form.Group controlId="formRootNote">
                  <Form.Label>Root Note</Form.Label>
                  <Form.Select onChange={(event) => setRootNote((event.target as HTMLInputElement).value)}>
                    {Notes.getNotes().map((val) => (
                      <option key={val}>{val}</option>
                    ))}
                  </Form.Select>
                </Form.Group>
              </Col>
              <Col xs={6} md={4}>
                <Form.Group controlId="formInterval">
                  <Form.Label>Highest Interval</Form.Label>
                  <Form.Select onChange={(event) => setInterval((event.target as HTMLInputElement).value)}>
                    {Notes.intervals.map((val) => (
                      <option key={val[0]}>{val[0]}</option>
                    ))}
                  </Form.Select>
                </Form.Group>
              </Col>

              {/* Have this column take up the whole width on mobile */}
              <Col xs={12} md={4}>
                <Form.Group controlId="formNumberOfNotes">
                  <Form.Label>Number of Notes</Form.Label>
                  <Form.Control
                    type="number"
                    defaultValue={Config.initialNumberOfNotes}
                    onChange={(event) => setNumberOfNotes(event.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col xs={12} md={2}>
                <Form.Check type="checkbox" label="Random note duration" onChange={(e) => setUseRandomNoteDuration(e.target.checked)} />
              </Col>
              <Col>
                <Form.Check type="checkbox" label="Use chromatic scale" onChange={(e) => setUseChromaticScale(e.target.checked)} />
              </Col>
            </Row>
            <Row className="mb-3 mt-3">
              <Col>
                <Form.Group controlId="formInst">
                  <Form.Label>Instrument</Form.Label>
                  <Form.Select
                    onChange={(event) => {
                      const instStr = (event.target as HTMLInputElement).value;
                      if (Player.isInstrument(instStr)) {
                        setInstrument(instStr);
                      }
                    }}
                  >
                    <option value={"piano"}>{"Piano"}</option>
                    <option value={"guitar-acoustic"}>{"Guitar"}</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
          </GroupBox>
        </Form>
      </Col>
      <Row>
        <Col>
          <Stack direction="horizontal" gap={3}>
            <Button className="mt-3 bi bi-play" onClick={playRootNote}>
              Play Root
            </Button>
            <Button
              className="mt-3 bi bi-play"
              onClick={(e) => Player.playNotes(instrument, notes, noteDurations)}
              disabled={notes.length === 0}
            >
              Replay Melody
            </Button>
            <Button className="mt-3 ms-auto btn-success bi bi-arrow-clockwise" onClick={generateNotes}>
              New Melody
            </Button>
          </Stack>
        </Col>
      </Row>
    </Row>
  );
};

export default InputForm;
