import * as React from "react";
import { Form, Container, Row, Col, ButtonGroup,  ToggleButton } from "react-bootstrap";
import {Utils,useLocalState} from "../Utils"
interface IFretboardMasterProps {}

type DisplayNote = {
   displayText: string
   note: string
}

const FretboardMaster: React.FC<IFretboardMasterProps> = () => {

  const naturalNotes = ["C", "D", "E", "F", "G", "A", "B"];
  const chromaticNotes = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];
  const localStoreKey = 'FretboardMaster/selectedString';


  const [selectedStrings, setSelectedStrings] = useLocalState<boolean []>(localStoreKey,initialSelectedStrings);
  const [isNatural, setIsNatural] = React.useState(true);
  const [isFiveSecond, setIsFiveSecond] = React.useState(true);
  const [displayedNote, setDisplayedNote] = React.useState<DisplayNote>(()=>getNextDisplayString(''));
  const timeOutIDRef = React.useRef<NodeJS.Timeout>();

  function onStringSelected(index:number, isChecked: boolean){
    selectedStrings[index]=isChecked
    setSelectedStrings(selectedStrings.slice())
  }

  function initialSelectedStrings(){
    return Array.from({ length: 6 }).map((v,i) =>true);
  }

  const setChromatic = (val: string) => {
    setIsNatural(val==="natural")
  };
  
  const setSpeed = (val: string) => {
    setIsFiveSecond(val === "5");
  };

  //converts an array of the form [true,false,false,true,true,false] to [0,3,4]
  //i.e. an array containing just the indexes where the value is true
  function selectionArrayToSelectedIndex(selectionArray: boolean[]){
    return selectionArray.map( (v,i)=> v?i:-1 ).filter(v=> v>=0);
  }

  function getNextDisplayString(noteToAvoid:string):DisplayNote{
    const stringsToUse = selectionArrayToSelectedIndex(selectedStrings);
    const note = getRandomNote(noteToAvoid)
    if(stringsToUse.length===0)
      return  { displayText: note, note:note }

    const stringToDisplay = stringsToUse[ Utils.randomInteger(0, stringsToUse.length) ]+1;
    return { displayText: stringToDisplay + ' ' +note, note:note  } 
  }

  function getRandomNote(noteToAvoid:string):string {
    const notesToUse = (isNatural ? naturalNotes : chromaticNotes).filter(x=>x !== noteToAvoid)
    return notesToUse[Utils.randomInteger(0, notesToUse.length)]
  }
  
  React.useEffect(() => {
    if (timeOutIDRef.current) {
      clearTimeout(timeOutIDRef.current);
    }
    timeOutIDRef.current = setTimeout(() => {
      setDisplayedNote(getNextDisplayString(displayedNote.note));
    }, 1000 * (isFiveSecond ? 5 : 10));
    // eslint-disable-next-line
  }, [isFiveSecond,isNatural,selectedStrings,displayedNote]);

  return (
    <Container>
      <Form className="mt-3">
        <Row className="mb-3">
          <Col>
            <div onChange={(e) => setChromatic((e.target as HTMLInputElement).value)}>
              <Form.Check inline label="Chromatic" name="isNatural" type="radio" id="chromatic" value="chromatic" />
              <Form.Check inline label="Natural" name="isNatural" type="radio" id="natural" value="natural" defaultChecked={true} />
            </div>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <div onChange={(e) => setSpeed((e.target as HTMLInputElement).value)}>
              <Form.Check inline label="5 seconds" name="speed" type="radio" id="5sec" value="5" defaultChecked={true} />
              <Form.Check inline label="10 seconds" name="speed" type="radio" id="10sec" value="10" />
            </div>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
          <ButtonGroup className="mb-2">{
            Array.from({ length: 6 }).map((_, index) => (
              <ToggleButton 
                type="checkbox"  
                variant="outline-primary" 
                checked={selectedStrings[index]} 
                value={index} 
                id={`radio-${index}`} key={index} 
                onChange={(e) => onStringSelected(index,e.currentTarget.checked) } >
                  {index+1}
                </ToggleButton>
          ))}
           
            
            </ButtonGroup>
          </Col>
        </Row>

        <Row className="mb-3">
          <div style={{ color: "black", fontSize: "150px", textAlign: "center", padding: "6px" }}> 
            { displayedNote.displayText}
          </div>
        </Row>
      </Form>
    </Container>
  );
};

export default FretboardMaster;
